import * as React from 'react';
import { connect } from 'react-redux';
import { IWithUser, IWIthConnector } from '../reducers/reducers';
import { Row, Col, message } from 'antd';
import UserForm from './forms/UserForm';
import UserDTO from '../dto/UserDTO';
import { RouterProps } from 'react-router';
import { ISetUserDataProps, setUserData } from '../actions/action';

interface IPersonalAreaProps
  extends IWithUser,
    IWIthConnector,
    ISetUserDataProps,
    RouterProps {}

class PersonalArea extends React.Component<IPersonalAreaProps> {
  private _onCancel = () => {
    this.props.history.goBack();
  };
  private _onSave = (user: UserDTO) => {
    this.props.connector
      .publishUser(user)
      .then(res => {
        this.props.setUserData(user);
        message.success('Salvataggio completato');
      })
      .catch(err => {
        console.error(`_onSave: error ${err}`);
        message.error('Si  verificato un errore nel salvataggio');
      });
  };

  public render() {
    const { user } = this.props;
    return (
      <Row>
        <Col span={6}></Col>
        <Col span={12} style={{ marginTop: '10px' }}>
          <UserForm
            loggedUserId={(user as UserDTO).id}
            user={user}
            personalArea={true}
            onCancel={this._onCancel}
            onSave={this._onSave}
          />
        </Col>
        <Col span={6}></Col>
      </Row>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector,
    committee: state.appData.committee
  };
};
const PersonalAreaContainer = connect(mapStateToProps, {
  setUserData
})(PersonalArea);
export default PersonalAreaContainer;
