import CommitteeDTO from '../dto/CommitteeDTO';
import UserDTO from '../dto/UserDTO';

export enum ReduxActionTypes {
  SET_SHOW_LOADING = 'SET_SHOW_LOADING',
  SET_USER_DATA = 'SET_USER_DATA',
  SET_COMMITTEE_DATA = 'SET_COMMITTEE_DATA',
  RESET_AUTH_DATA = 'RESET_AUTH_DATA',
  SET_USERS_DATA = 'SET_USERS_DATA'
}

export interface IReduxBaseAction {
  type: ReduxActionTypes;
}

export interface IReduxSetUserData extends IReduxBaseAction {
  type: ReduxActionTypes.SET_USER_DATA;
  data: {
    user: UserDTO;
  };
}

export interface IReduxSetCommitteeData extends IReduxBaseAction {
  type: ReduxActionTypes.SET_COMMITTEE_DATA;
  data: {
    committee: CommitteeDTO;
  };
}

export interface IReduxSetUsersData extends IReduxBaseAction {
  type: ReduxActionTypes.SET_USERS_DATA;
  data: {
    users: UserDTO[];
  };
}

export interface IReduxSetShowLoading extends IReduxBaseAction {
  type: ReduxActionTypes.SET_SHOW_LOADING;
  data: boolean;
}

export interface IReduxResetAuthData extends IReduxBaseAction {
  type: ReduxActionTypes.RESET_AUTH_DATA;
}

export type TReducerActions =
  | IReduxSetUserData
  | IReduxSetCommitteeData
  | IReduxSetShowLoading
  | IReduxResetAuthData
  | IReduxSetUsersData;

export interface ISetUsersDataProps {
  setUsersData(users: UserDTO[]): IReduxSetUsersData;
}

export interface ISetUserDataProps {
  setUserData(user: UserDTO): IReduxSetUserData;
}

export interface ISetCommitteeDataProps {
  setCommitteeData(committee: CommitteeDTO): IReduxSetCommitteeData;
}

export interface IResetAuthDataProps {
  resetAuthData(): IReduxResetAuthData;
}

export function setUsersData(users: UserDTO[]): IReduxSetUsersData {
  return {
    type: ReduxActionTypes.SET_USERS_DATA,
    data: {
      users
    }
  };
}

export function setUserData(user: UserDTO): IReduxSetUserData {
  return {
    type: ReduxActionTypes.SET_USER_DATA,
    data: {
      user
    }
  };
}

export function setCommitteeData(
  committee: CommitteeDTO
): IReduxSetCommitteeData {
  return {
    type: ReduxActionTypes.SET_COMMITTEE_DATA,
    data: {
      committee
    }
  };
}

export function resetAuthData(): IReduxResetAuthData {
  return {
    type: ReduxActionTypes.RESET_AUTH_DATA
  };
}
