import * as React from 'react';
import {
  Button,
  Col,
  Form,
  message,
  Modal,
  Row,
  Input,
  Select,
  Icon
} from 'antd';
import { connect } from 'react-redux';
import { IWithUser, IWIthConnector } from '../reducers/reducers';
import {
  setUserData,
  setCommitteeData,
  setUsersData,
  ISetUserDataProps,
  ISetCommitteeDataProps,
  ISetUsersDataProps
} from '../actions/action';
import { Connector } from '../utils/Connector';
import CommitteeDTO from '../dto/CommitteeDTO';
import { FormComponentProps } from 'antd/lib/form/Form';
import { Links } from '../utils/Links';
import { RouterProps } from 'react-router';
import { PortalName } from '../utils/Constants';

const { Option } = Select;

interface ILoginFormState {
  committees: CommitteeDTO[];
  loading: boolean;
}

class LoginForm extends React.Component<
  IWithUser &
    IWIthConnector &
    ISetUserDataProps &
    ISetCommitteeDataProps &
    ISetUsersDataProps &
    FormComponentProps &
    RouterProps,
  ILoginFormState
> {
  state = {
    committees: [],
    loading: false
  };

  connector: Connector | undefined;

  componentDidMount() {
    this.props.connector
      .getCommittees()
      .then(res => {
        this.setState(
          {
            ...this.state,
            committees: res.data
          },
          () => {
            if (res.data && res.data[0]) {
              this.handleSelectChange(res.data[0].id);
              // TODO: da rimuovere
              this.props.form.setFieldsValue({
                username: 'f.marchetti',
                password: 'default'
              });
            }
          }
        );
      })
      .catch(err => {
        console.log(err);
      });
  }

  handleSelectChange = (value: number) => {
    this.props.form.setFieldsValue({
      committeeId: value
    });
  };

  private buildCommitteesSelect = () => {
    const { committees } = this.state;
    if (committees.length === 0) {
      return <Icon type="loading" />;
    }

    const { getFieldDecorator } = this.props.form;
    return (
      <Form.Item>
        {getFieldDecorator('committeeId', {
          rules: [{ required: true, message: 'Selezionare il comitato' }]
        })(
          <Select onChange={this.handleSelectChange}>
            {committees.map((committee: CommitteeDTO) => (
              <Option key={committee.id} value={committee.id}>
                {committee.description}
              </Option>
            ))}
          </Select>
        )}
      </Form.Item>
    );
  };

  handleSubmit = (e: any) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        this.setState({ loading: true }, () => {
          const committeeId = values.committeeId;
          this.props.connector
            .findUserByUsername(values.username, values.password)
            .then(res => {
              this.setState({ loading: false });
              const user = res.data;
              const committee_users: Array<any> = user.committee_users;
              if (committee_users) {
                const founded = committee_users.find(
                  element => element.committeeId === committeeId
                );
                if (!founded) {
                  message.error('Utente non abilitato al comitato selezionato');
                } else if (founded.disabled) {
                  message.error('Utente non abilitato al comitato selezionato');
                } else {
                  this.props.setUserData(user);
                  this.props.setCommitteeData(founded.committee);
                  this.props.connector
                    .getUsers(committeeId)
                    .then(uRes => {
                      message.success('Autenticato');
                      this.props.setUsersData(uRes.data);
                      this.props.history.push(Links.SERVICES);
                    })
                    .catch(uErr => {
                      message.error(
                        `Si  verificato un errore nel recupero degli utenti`
                      );
                      console.log(`Error retrieving users. Error: ${uErr}`);
                    });
                }
              } else {
                message.error('Utente non abilitato al comitato selezionato');
              }
            })
            .catch(err => {
              this.setState({ loading: false }, () =>
                message.error('Username e password non validi')
              );
              console.error(err);
            });
        });
      }
    });
  };

  public render() {
    const { getFieldDecorator } = this.props.form;
    const { committees } = this.state;
    return (
      <Modal
        visible={true}
        closable={false}
        confirmLoading={false}
        footer={null}
      >
        <div style={{ textAlign: 'right' }}>
          <b>{PortalName}</b>
        </div>
        <Row gutter={8}>
          <Col span={24}>
            <img
              alt={''}
              src={process.env.PUBLIC_URL + '/red_cross_logo.png'}
              width="100%"
            />
          </Col>
        </Row>
        <Form onSubmit={this.handleSubmit} className="login-form">
          <Form.Item>
            {getFieldDecorator('username', {
              rules: [{ required: true, message: 'Username obbligatoria' }]
            })(
              <Input
                prefix={
                  <Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />
                }
                placeholder="Digitare il proprio username"
              />
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('password', {
              rules: [{ required: true, message: 'Password obbligatoria' }]
            })(
              <Input
                prefix={
                  <Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />
                }
                type="password"
                placeholder="Digitare la propria password"
              />
            )}
          </Form.Item>
          {this.buildCommitteesSelect()}
          <Form.Item>
            <Button
              loading={this.state.loading}
              disabled={
                committees === null ||
                committees === undefined ||
                committees.length === 0
              }
              type="primary"
              htmlType="submit"
              style={{ width: '100%' }}
              className="login-form-button"
            >
              Log in
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    );
  }
}

const WrappeLoginForm = Form.create({ name: 'normal_login' })(LoginForm);
const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    connector: state.appData.connector
  };
};
const LoginContainer = connect(mapStateToProps, {
  setUserData,
  setCommitteeData,
  setUsersData
})(WrappeLoginForm);
export default LoginContainer;
