import { combineReducers } from 'redux';
import { TReducerActions, ReduxActionTypes } from '../actions/action';
import { Connector } from '../utils/Connector';
import UserDTO from '../dto/UserDTO';
import CommitteeDTO from '../dto/CommitteeDTO';

export interface IAppDataState
  extends IWithUser,
    IWithCommittee,
    IWIthConnector {
  user: UserDTO | undefined;
  committee: CommitteeDTO | undefined;
  users: UserDTO[];
  showLoading: boolean;
  connector: Connector;
}

const stateDefault: IAppDataState = {
  user: undefined,
  committee: undefined,
  users: [],
  connector: new Connector('', ''),
  showLoading: false
};

export interface IWithUser {
  user: UserDTO | undefined;
}

export interface IWithUsers {
  users: UserDTO[];
}

export interface IWithCommittee {
  committee: CommitteeDTO | undefined;
}

export interface IWIthConnector {
  connector: Connector;
}

const appData = (
  state: IAppDataState = stateDefault,
  action: TReducerActions
): IAppDataState => {
  switch (action.type) {
    case ReduxActionTypes.SET_USER_DATA:
      const { user } = action.data;
      let connector: any;
      let normalizedUser = (user && { ...user }) || undefined;
      if (user) {
        connector = new Connector(user.username, user.password);
        if ((user as any).committee_users) {
          normalizedUser = {
            ...user,
            committeeUser: (user as any).committee_users[0]
          };
        }
      }
      const newState = {
        ...state,
        user: normalizedUser,
        connector
      };
      return newState;
    case ReduxActionTypes.SET_COMMITTEE_DATA:
      const { committee } = action.data;
      return {
        ...state,
        committee
      };
    case ReduxActionTypes.SET_SHOW_LOADING:
      return { ...state, showLoading: action.data };
    case ReduxActionTypes.RESET_AUTH_DATA:
      return {
        ...state,
        user: undefined,
        committee: undefined,
        connector: new Connector('', ''),
        users: []
      };
    case ReduxActionTypes.SET_USERS_DATA:
      const { users } = action.data;
      const normalizedUsers: any = (users || []).map(
        (userToNormalize: UserDTO) => {
          return {
            ...userToNormalize,
            committeeUser: (userToNormalize as any).committee_users[0]
          };
        }
      );
      return {
        ...state,
        users: normalizedUsers
      };
    default:
      return state;
  }
};

const AppReducers = combineReducers({
  appData
});

export default AppReducers;
